.button {
  position: relative;
  font-weight: bold;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: $accentOrange;

  &--decoration {
    position: absolute;
    top: 1px;

    &-place {
      display: inline-block;
      margin-right: 8px;
      width: 16px;
      height: 16px;
    }
  }
}
